
/**
 * vuex管理登陆状态，具体可以参考官方登陆模板示例
 */
import {
	mapMutations
} from 'vuex';
export default {
	methods: {
		...mapMutations(['updateLogin'])
	},
	onLaunch: function() {
		console.log('onLaunch');
		let userInfo = uni.getStorageSync('userInfo') || '';
		if(userInfo){
			//更新登陆状态
			uni.getStorage({
				key: 'userInfo',
				success: (res) => {
					this.updateLogin(res.data);
				}
			});
		}
		
	},
	onShow: function() {
		console.log('App Show')
	},
	onHide: function() {
		console.log('App Hide')
	},
}
